
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import axios from 'axios'
import { getItem } from '../../utils/storage'
import { userInfoTran } from '../../utils/dataTran'
export default {
components: {
XHeader,
Search,
Checklist,
PopupHeader,
Popup,
Group,
Radio
},
data () {
return {
results: [],
value: '',
chooseItem: '',
list: [],
chooseIds: [],
fileId: '',
userId: getItem('user').id,
options: [{
key: 0,
value: '只读'
}, {
key: 1,
value: '编辑'
}],
show: false,
opt: ''
}
},
mounted () {
this.fileId = this.$route.params.fileId || ''
this.getResult()
},
methods: {
change (value) {
this.chooseIds = value
},
getResult (val) {
axios
.get(`/api/getfriendslist`, {
params: {
userId: this.userId,
val: val || ''
}
})
.then((res) => {
const data = res.data.status && userInfoTran(res.data.list, false)
data.forEach((el, index) => {
this.list.push({
key: el.id,
value: el.name
})
})
}).catch((error) => {
console.log(error)
})
},
shareFile () {
const body = {
userId: this.userId,
friendIds: this.chooseIds,
fileId: this.fileId,
fileAuth: this.opt
}
const _this = this
axios
.post(`/api/sharefile`, body)
.then((res) => {
res.data && AlertModule.show({
title: '分享成功',
onHide () {
_this.$router.push({ path: '/note/list' })
}
})
}).catch((error) => {
console.log(error)
})
}
}
}



