
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import axios from 'axios'
import { getItem } from '../../utils/storage'
var draw
var preHandler = function (e) {
e.preventDefault()
}
class Draw {
constructor (el) {
this.el = el
this.canvas = document.getElementById(this.el)
this.cxt = this.canvas.getContext('2d')
this.stage_info = this.canvas.getBoundingClientRect()
this.path = {
beginX: 0,
beginY: 0,
endX: 0,
endY: 0
}
}
init (btn) {
var that = this
this.canvas.addEventListener('touchstart', function (event) {
document.addEventListener('touchstart', preHandler, false)
that.drawBegin(event)
})
this.canvas.addEventListener('touchend', function (event) {
document.addEventListener('touchend', preHandler, false)
that.drawEnd()
})
this.clear(btn)
}
drawBegin (e) {
var that = this
window.getSelection() ? window.getSelection().removeAllRanges() : document.selection.empty()
this.cxt.strokeStyle = '#000'
this.cxt.beginPath()
this.cxt.moveTo(
e.changedTouches[0].clientX - this.stage_info.left,
e.changedTouches[0].clientY - this.stage_info.top
)
this.path.beginX = e.changedTouches[0].clientX - this.stage_info.left
this.path.beginY = e.changedTouches[0].clientY - this.stage_info.top
this.canvas.addEventListener('touchmove', function () {
that.drawing(event)
})
}
drawing (e) {
this.cxt.lineTo(
e.changedTouches[0].clientX - this.stage_info.left,
e.changedTouches[0].clientY - this.stage_info.top
)
this.path.endX = e.changedTouches[0].clientX - this.stage_info.left
this.path.endY = e.changedTouches[0].clientY - this.stage_info.top
this.cxt.stroke()
}
drawEnd () {
document.removeEventListener('touchstart', preHandler, false)
document.removeEventListener('touchend', preHandler, false)
document.removeEventListener('touchmove', preHandler, false)

}
clear (btn) {
this.cxt.clearRect(0, 0, 300, 600)
}
save () {
return this.canvas.toDataURL()
}
}

export default {
data () {
return {
msg: 'Welcome to Your Vue.js App',
val: true,
url: '',
title: '',
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
show: false,
opt: 0,
userId: getItem('user').id
}
},
components: {
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio,
AlertModule
},
mounted () {
draw = new Draw('canvas')
draw.init()
},
methods: {
clear () {
draw.clear()
},
save () {
const file = draw.save()
const fileName = this.title === '' ? `手写_${Math.round(new Date().getTime() / 1000)}` : this.title
const otherinfo = JSON.stringify({
userId: this.userId,
title: fileName,
type: this.opt,
fileType: 'img'
})
let param = new FormData() 
param.append('file', file) 
param.append('otherinfo', otherinfo) 
let config = {
headers: {'Content-Type': 'multipart/form-data'}
}

axios.post(`/api/savemedia`, param, config)
.then(res => {
res.data && this.$router.push('/note/list')
})
},
mutate (word) {
this.$emit('input', word)
}
}
}



