
import XHeader from 'vux/src/components/x-header/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Search from 'vux/src/components/search/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import axios from 'axios'
import { userInfoTran } from '../../utils/dataTran'
import { getItem } from '../../utils/storage'
export default {
components: {
XHeader,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Search,
Cell,
Confirm
},
data () {
return {
userId: getItem('user').id,
results: [],
value: '',
list: [],
show: false,
currentIndex: 0
}
},
mounted () {
this.getResult()
},
methods: {
getResult (val) {
axios
.get(`/api/getfriendslist`, {
params: {
userId: this.userId,
val: val || '',
status: 2
}
})
.then((res) => {
this.list = res.data.status && userInfoTran(res.data.list, false)
}).catch((error) => {
console.log(error)
})
},
dele () {
const body = {
userId: this.userId,
friendId: this.list[this.currentIndex].id
}
axios.post(`/api/deletefriend`, body)
.then((res) => {
res.data && this.getResult()
}).catch((error) => {
console.log(error)
})
}
}
}



