
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import { getItem } from '../../utils/storage'
import axios from 'axios'

export default {
components: {
Group,
Cell,
CellBox,
XHeader,
XInput
},
data () {
return {
pwd: '',
rePwd: '',
name: getItem('user').name,
userId: getItem('user').id
}
},
methods: {
save () {
const _this = this
this.pwd === this.rePwd
? axios
.post(`/api/adduser`, {
userId: this.userId,
newPwd: this.pwd
})
.then((res) => {
res.status
? AlertModule.show({
content: '修改成功！',
onHide () {
_this.$router.replace('/mine')
}
})
: AlertModule.show({
content: '请求失败！'
})
}).catch((error) => {
console.log(error)
})
: AlertModule.show({
content: '两次输入密码不一致！',
onHide () {
_this.pwd = ''
_this.rePwd = ''
}
})
}
}
}



