
import Icon from 'vue-awesome/components/Icon'
import 'font-awesome/css/font-awesome.min.css'
import axios from 'axios'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Search from 'vux/src/components/search/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import { userInfoTran, friendInfoTran } from '../../utils/dataTran'
import { getItem } from '../../utils/storage'

export default {
components: {
Icon,
Group,
Cell,
CellBox,
Search,
XDialog,
XHeader
},
data () {
return {
list: [],
userId: getItem('user').id
}
},
mounted () {
this.getAddedFriends()
},
methods: {
add (index) {
const body = {
userId: this.userId,
friendId: this.list[index].id
}
const _this = this
axios
.post(`/api/addfriend`, body)
.then((res) => {
if (res.data) {
AlertModule.show({
title: '请求已发送！',
onHide () {
_this.getAddedFriends()
}
})
}
}).catch((error) => {
console.log(error)
})
},
updateFriend (id, status) {
const _this = this
const body = {
userId: this.userId,
friendId: id,
status
}
axios
.post(`/api/updatefriend`, body)
.then((res) => {
if (res.data) {
_this.getAddedFriends()
}
}).catch((error) => {
console.log(error)
})
},
getAddedFriends (val) {
axios
.get(`/api/getfriendslist`, {
params: {
userId: this.userId,
val: val || '',
status: 0
}
})
.then((res) => {
this.list = res.data.status && friendInfoTran(res.data.list)
}).catch((error) => {
console.log(error)
})
},

scan () {
this.$router.push('/mine/qrcode/scanner')
},
getResult (val) {
val !== '' && axios
.get(`/api/getuserlist`, {
params: {
userId: this.userId,
val: val || ''
}
})
.then((res) => {
this.list = res.data.status && userInfoTran(res.data.list, false)
}).catch((error) => {
console.log(error)
})
}
}
}



