
import 'mavon-editor/dist/css/index.css'
import {mavonEditor} from 'mavon-editor'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from 'axios'
import { getItem } from '../../utils/storage'
import { fileInfoTran } from '../../utils/dataTran'

export default {
data () {
return {
item: {},
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
show: false,
show1: false,
opt: 0,
userId: getItem('user').id,
multiVer: false,
everVer: [],
change: false
}
},
components: {
mavonEditor,
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio,
XDialog,
Cell
},
mounted () {
const { fileId, fileAuth } = this.$route.params
!fileId
? this.item = {
title: '',
content: '',
type: 0,
fileType: 'md',
id: ''
}
: axios.get(`/api/getfileinfo`, {
params: {
fileId
}})
.then((res) => {
if (res.data.status) {
const data = fileInfoTran(res.data.list)
this.item = {
...data[0],
fileAuth
}
if (res.data.list.length > 1) {
this.multiVer = true
this.everVer = data.slice(1)
}
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
},
methods: {
backToList () {
this.$router.push({
path: this.item.id === '' ? '/note/add' : '/note/list'
})
},
save () {
this.item.id === '' ? this.show = true : this.saveFile()
},
saveFile () {
this.show = false
const body = {
...this.item,
userId: this.userId,
fileId: this.item.id,
title: this.item.title === '' ? '无标题' : this.item.title,
type: this.item.id === '' ? this.opt : this.item.type
}
this.change
? (this.item.fileAuth === 0
? AlertModule.show({
content: '您只有读取权限，无法提交修改！'
})
: axios
.post(`/api/savefile`, body)
.then((res) => {
const opt = res.data.status && res.data.isDiff ? {
name: 'noteShow',
params: {
item: fileInfoTran([res.data.fileInfo], true, res.data.isDiff)
}
} : {
path: '/note/list'
}
this.$router.push(opt)
}).catch((error) => {
console.log(error)
})) : this.$router.push({
path: '/note/list'
})
},
retVer (index) {
this.show1 = false
this.item = this.everVer[index]
}
}
}



