
import XInput from 'vux/src/components/x-input/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

import vueLoading from 'vue-loading-template'
import axios from 'axios'
import { getItem } from '../../utils/storage'
export default {
components: {
XButton,
XHeader,
PopupHeader,
Popup,
Group,
Radio,
XInput,
Box,
XProgress,
vueLoading
},
data () {
return {
show: false,
isOpen: true,
duration: 0,
currentTime: 0,
recordIng: false,
item: {},
title: '',
userId: getItem('user').id,
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
opt: 0,
voiceOpt: '播放'
}
},
created () {
this.item = this.$route.params.item
const blob = this.base64ToBlob(this.item.content)
this.item.url = URL.createObjectURL(blob)
},
mounted () {
},
methods: {
base64ToBlob (dataurl) {
const arr = dataurl.split(',')
const mime = arr[0].match(/:(.*?);/)[1]
const bstr = atob(arr[1])
let n = bstr.length
const u8arr = new Uint8Array(n)
while (n--) {
u8arr[n] = bstr.charCodeAt(n)
}
return new Blob([u8arr], {type: mime})
},
updateTime (e) {
this.currentTime = (e.target.currentTime / this.duration) * 100
if (this.currentTime === 100) {
this.voiceOpt = '播放'
this.currentTime = 0
}
},
ready () {
},
error () {
},
ended () {
},
showVoice () {
this.isOpen = !this.isOpen
if (this.isOpen) {
this.voiceOpt = '暂停'
this.duration = this.$refs.audio.duration
this.$refs.audio.play()
} else {
this.voiceOpt = '播放'
this.$refs.audio.pause()
}
},
saveFile () {
const file = this.item.content
const fileName = this.title === '' ? `语音_${Math.round(new Date().getTime() / 1000)}` : this.title
const otherinfo = JSON.stringify({
userId: this.userId,
title: fileName,
type: this.opt,
fileType: 'voice'
})
let param = new FormData() 
param.append('file', file) 
param.append('otherinfo', otherinfo) 
let config = {
headers: {'Content-Type': 'multipart/form-data'}
}

axios.post(`/api/savemedia`, param, config)
.then(res => {
res.data && this.$router.push('/note/list')
})
}
}
}



