
import Vue from 'vue'
import VueHtml5Editor from 'vue-html5-editor'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import axios from 'axios'
import { getItem } from '../../utils/storage'
import { fileInfoTran } from '../../utils/dataTran'

export default {
name: 'h5Editor',
data () {
return {
item: {},
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
show: false,
show1: false,
opt: 0,
userId: getItem('user').id,
multiVer: false,
everVer: [],
change: false
}
},
components: {
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio,
XDialog,
Cell
},
mounted () {
const { fileId, fileAuth } = this.$route.params
!fileId
? this.item = {
title: '',
content: '',
type: 0,
fileType: 'html',
id: ''
}
: axios.get(`/api/getfileinfo`, {
params: {
fileId
}})
.then((res) => {
if (res.data.status) {
const data = fileInfoTran(res.data.list)
this.item = {
...data[0],
fileAuth
}
if (res.data.list.length > 1) {
this.multiVer = true
this.everVer = data.slice(1)
}
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
},
methods: {
backToList () {
this.$router.push({
path: this.item.id === '' ? '/note/add' : '/note/list'
})
},
updateData (val) {
this.item.content = val
this.change = true
},
save () {
this.item.id === '' ? this.show = true : this.saveFile()
},
saveFile () {
this.show = false
const body = {
...this.item,
userId: this.userId,
fileId: this.item.id,
title: this.item.title === '' ? '无标题' : this.item.title,
type: this.item.id === '' ? this.opt : this.item.type
}
this.change
? (this.item.fileAuth === 0
? AlertModule.show({
content: '您只有读取权限，无法提交修改！'
})
: axios
.post(`/api/savefile`, body)
.then((res) => {
const opt = res.data.status && res.data.isDiff ? {
name: 'noteShow',
params: {
item: fileInfoTran([res.data.fileInfo], true, res.data.isDiff)
}
} : {
path: '/note/list'
}
this.$router.push(opt)
}).catch((error) => {
console.log(error)
})) : this.$router.push({
path: '/note/list'
})
},
retVer (index) {
this.item = this.everVer[index]
this.show1 = false
}
}
}
Vue.use(VueHtml5Editor, {

name: 'vue-html5-editor',


showModuleName: false,


icons: {
text: 'fa fa-pencil',
color: 'fa fa-paint-brush',
font: 'fa fa-font',
align: 'fa fa-align-justify',
list: 'fa fa-list',
link: 'fa fa-chain',
unlink: 'fa fa-chain-broken',
tabulation: 'fa fa-table',
image: 'fa fa-file-image-o',
hr: 'fa fa-minus',
eraser: 'fa fa-eraser',
undo: 'fa-undo fa',
'full-screen': 'fa fa-arrows-alt',
info: 'fa fa-info'
},


image: {

sizeLimit: 512 * 1024,


upload: {
url: null,
headers: {},
params: {},
fieldName: {}
},



compress: {
width: 1600,
height: 1600,
quality: 80
},


uploadHandler (responseText) {

var json = JSON.parse(responseText)
if (!json.ok) {
alert(json.msg)
} else {
return json.data
}
}
},


language: 'zh-cn',

i18n: {

'zh-cn': {
'align': '对齐方式',
'image': '图片',
'list': '列表',
'link': '链接',
'unlink': '去除链接',
'table': '表格',
'font': '文字',
'full screen': '全屏',
'text': '排版',
'eraser': '格式清除',
'info': '关于',
'color': '颜色',
'please enter a url': '请输入地址',
'create link': '创建链接',
'bold': '加粗',
'italic': '倾斜',
'underline': '下划线',
'strike through': '删除线',
'subscript': '上标',
'superscript': '下标',
'heading': '标题',
'font name': '字体',
'font size': '文字大小',
'left justify': '左对齐',
'center justify': '居中',
'right justify': '右对齐',
'ordered list': '有序列表',
'unordered list': '无序列表',
'fore color': '前景色',
'background color': '背景色',
'row count': '行数',
'column count': '列数',
'save': '确定',
'upload': '上传',
'progress': '进度',
'unknown': '未知',
'please wait': '请稍等',
'error': '错误',
'abort': '中断',
'reset': '重置'
}
},


hiddenModules: [],



visibleModules: [
'text',
'color',
'font',
'align',
'list',
'link',
'unlink',
'tabulation',
'image',
'hr',
'eraser',
'undo',
'full-screen',
'info'
],


modules: {

}
})



