
import Masker from 'vux/src/components/masker/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import axios from 'axios'
import { getItem } from '../../utils/storage'

export default {
data () {
return {
list: [{
title: '新建笔记',
desc: '输入文字以创建笔记',
target: 'input',
img: require('@/assets/note-add-input.jpg')
}, {
title: '新建MarkDown',
desc: '以markdown的形式创建笔记',
target: 'markdown',
img: require('@/assets/note-add-input.jpg')
}, {
title: '新建语音笔记',
desc: '随时随地记录灵感',
target: 'voice',
img: require('@/assets/note-add-input.jpg')
}, {
title: '新建图片文件',
desc: '拍照或上传图片以创建笔记',
target: 'img',
img: require('@/assets/note-add-scan.jpg')
}, {
title: '新建手写笔记',
desc: '使用画笔创建手写笔记',
target: 'canvas',
img: require('@/assets/note-add-upload.png')
}],
options: [{
key: 0,
value: '工作文件'
}, {
key: 1,
value: '生活文件'
}],
show: false,
opt: -1,
title: '',
file: {},
userId: getItem('user').id
}
},
methods: {
onClickMask (item) {
item.target === 'img'
? this.$refs.photo.dispatchEvent(new MouseEvent('click'))
: this.$router.push(`/note/add/${item.target}`)
},
saveImg (e) {
this.file = e.target.files[0]
this.show = true
},
save () {
const reader = new FileReader()
const _this = this
reader.onloadend = function () {
const dataURL = reader.result
const fileName = _this.title === '' ? `图片_${Math.round(new Date().getTime() / 1000)}` : _this.title
const otherinfo = JSON.stringify({
userId: _this.userId,
title: fileName,
type: _this.opt,
fileType: 'img'
})
console.log(otherinfo)
let param = new FormData() 
param.append('file', dataURL) 
param.append('otherinfo', otherinfo) 
let config = {
headers: {'Content-Type': 'multipart/form-data'}
}

axios.post(`/api/savemedia`, param, config)
.then(res => {
res.data && _this.$router.push('/note/list')
})
}
reader.readAsDataURL(this.file) 
}
},
components: {
Masker,
XDialog,
XInput,
Group,
Radio
}
}



