
import {QrcodeReader} from 'vue-qrcode-reader'
import 'font-awesome/css/font-awesome.min.css'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import axios from 'axios'
import { friendInfoTran } from '../../utils/dataTran'
import { getItem } from '../../utils/storage'
export default {
data () {
return {
value: '',
show: false,
paused: false,
userInfo: {},
userId: getItem('user').id
}
},
components: {
QrcodeReader,
Group,
Cell,
CellBox,
XHeader,
XButton,
XDialog
},
methods: {
async onDecode (content) {
if (content) {
this.paused = true
axios.get(`/api/getuserinfo`, {
params: {
userId: this.userId,
friendId: content
}
})
.then((res) => {
this.userInfo = res.data.status && friendInfoTran(res.data.list)[0]
switch (this.userInfo.friendStatus) {
case 0: this.userInfo.status = '已发送'
break
case 1: this.userInfo.status = '同意'
break
case 2: this.userInfo.status = '已是好友'
break
case 3: this.userInfo.status = '已被拒绝'
break
case undefined: this.userInfo.status = '加好友'
break
default: this.userInfo.status = '加好友'
break
}
console.log(this.userInfo)
this.show = true
}).catch((error) => {
console.log(error)
})
}
},
addFriend () {
this.userInfo.status === '加好友'
? this.add()
: this.updateFriend()
},
add () {
const body = {
userId: this.userId,
friendId: this.userInfo.id
}
const _this = this
axios
.post(`/api/addfriend`, body)
.then((res) => {
if (res.data) {
AlertModule.show({
title: '请求已发送！',
onHide () {
_this.$router.push({
path: '/mine/newfriend'
})
}
})
}
}).catch((error) => {
console.log(error)
})
},
updateFriend () {
const body = {
userId: this.userId,
friendId: this.userInfo.id,
status: 2
}
axios
.post(`/api/updatefriend`, body)
.then((res) => {
if (res.data) {
this.$router.push('/mine/newfriend')
}
}).catch((error) => {
console.log(error)
})
}
}
}



