
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import 'font-awesome/css/font-awesome.min.css'
import Icon from 'vue-awesome/components/Icon'
import axios from 'axios'
import {userInfoTran} from '../utils/dataTran'
import { setItem, getItem } from '../utils/storage'
export default {
data () {
return {
userName: getItem('user').name || '',
userPwd: getItem('user').pwd || ''
}
},
components: {
XInput,
Flexbox,
FlexboxItem,
Icon,
Group
},
methods: {
login () {
this.userName === '' || this.userPwd === ''
? AlertModule.show({
content: '请填写用户名和密码！'
})
: axios
.get(`/api/searchuser`, {
params: {
name: this.userName,
pwd: this.userPwd
}
}).then((res) => {
if (res.data.status) {
const user = userInfoTran(res.data.list, true)
setItem('user', user)
this.$router.push({path: '/app'})
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
this.$vux.toast.text(error || '登录失败')
})
}
}
}



