
import Recorder from '@/../static/recorder.browser.min.js'
import vueLoading from 'vue-loading-template'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'


let audioContext
let recorder
export default {
components: {
XButton,
vueLoading,
Box,
XHeader,
PopupHeader,
Popup,
Group,
Radio
},
data () {
return {
recordIng: false
}
},
mounted () {
try {

window.AudioContext = window.AudioContext || window.webkitAudioContext
navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia
window.URL = window.URL || window.webkitURL
audioContext = new AudioContext()
console.log('Audio context set up.')
console.log('navigator.getUserMedia ' + (navigator.getUserMedia ? 'available.' : 'not present!'))
} catch (e) {
alert('No web audio support in this browser!')
}
navigator.getUserMedia({
audio: true
}, this.startUserMedia, function (e) {
console.log('No live audio input: ' + e)
})
},
methods: {
startUserMedia (stream) {
var input = audioContext.createMediaStreamSource(stream)
console.log('Media stream created.')
input.connect(audioContext.destination)
console.log('Input connected to audio context destination.')
recorder = new Recorder(input)
console.log('Recorder initialised.')
},
startRecording (button) {
console.log('startRecording', recorder)
recorder && recorder.record()
this.recordIng = true
console.log('Recording...')
},
stopRecording (button) {
recorder && recorder.stop()
this.recordIng = false
this.createDownloadLink()

},
async createDownloadLink () {
const _this = this
recorder && recorder.exportWAV(function (blob) {
_this.blobToDataURL(blob, (re) => {
_this.$router.push({
name: 'ShowVoice',
params: {
item: {
fileType: 'voice',
content: re
}
}
})
})
})
},
blobToDataURL (blob, callback) {
var a = new FileReader()
a.onload = function (e) {
callback(e.target.result)
}
a.readAsDataURL(blob)
},
saveFile () {
}
}
}



