
import AlertModule from 'vux/src/plugins/alert/module'
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import axios from 'axios'
import { fileInfoTran } from '../../utils/dataTran'
import { getItem } from '../../utils/storage'
export default {
components: {
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Tab,
TabItem,
Confirm,
AlertModule,
PopupHeader,
Popup,
Radio,
Group,
Sticky
},
data () {
return {
value: '',
list: [],
index01: 0,
show: false,
currentIndex: 0,
confirmCont: '',
currentTabIndex: 0,
show1: false,
options: [{
key: 0,
value: '我的好友'
}],
shareType: '',
userId: getItem('user').id
}
},

mounted () {
this.getFileList(0, 0)
try {
window.URL = window.URL || window.webkitURL
} catch (error) {
alert('No web audio support in this browser!')
}
},
methods: {
base64ToBlob (dataurl) {
const arr = dataurl.split(',')
const mime = arr[0].match(/:(.*?);/)[0]
const bstr = atob(arr[1])
let n = bstr.length
const u8arr = new Uint8Array(n)
while (n--) {
u8arr[n] = bstr.charCodeAt(n)
}
return new Blob([u8arr], {type: mime})
},
getResult (val) {
this.getFileList(this.currentTabIndex, this.currentTabIndex === 3 ? 1 : 0, val)
},
change (value, label) {
this.shareType = value
},
dele () {
const item = this.list[this.currentIndex]
const body = {
userId: this.userId,
fileId: item.id
}
const url = item.isShare ? '/api/delsharefile' : '/api/deletefile'
axios.post(url, body)
.then((res) => {
!item.isShare && AlertModule.show({
title: '删除成功！',
content: this.list[this.currentIndex].isTrash ? '已永久删除！' : '已移到垃圾桶，如有需要可还原！'
})
this.getFileList(this.currentTabIndex, this.currentTabIndex === 3 ? 1 : 0)
}).catch((error) => {
console.log(error)
})
},
share () {

const re = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/
if (this.shareType === 0) {
this.$router.push({
name: 'choosefriend',
params: {
fileId: this.list[this.currentIndex].id
}
})
} else if (re.test(this.shareType) === true) {
console.log('分享至邮箱')
this.readySendMail(this.shareType)
} else {
AlertModule.show({
title: '邮箱格式错误！'
})
this.show1 = true
}
},
readySendMail (email) {
axios
.post(`/api/sendmail`, {
from: getItem('user').name,
to: email,
content: this.list[this.currentIndex].content
})
.then((res) => {
this.$vux.toast.text('分享成功')
this.show1 = false
}).catch((error) => {
this.$vux.toast.text(error || '分享失败')
})
},
revert (index) {
const body = {
userId: this.userId,
fileId: this.list[this.currentIndex].fileId
}
axios.post(`/api/revertfile`, body)
.then((res) => {
this.getFileList(this.currentTabIndex, this.currentTabIndex === 2 ? 1 : 0)
}).catch((error) => {
console.log(error)
})
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
switchTabitem (index) {
this.currentTabIndex = index
switch (index) {
case 0: this.getFileList(0, 0)
break
case 1: this.getFileList(1, 0)
break
case 2: this.getFileList(2, 0)
break
case 3: this.getFileList(3, 1)
break
default: this.getFileList(0, 0)
break
}
},
jumpToShow (index) {
const data = this.list[index]
console.log(data)
let opt = {}
switch (data.fileType) {
case 'md': opt = {
name: 'noteAddMakdown',
params: {
fileId: data.id,
fileAuth: data.fileAuth
}
}
break
case 'html': opt = {
name: 'noteShow',
params: {
item: data
}
}
break
case 'voice': opt = {
name: 'ShowVoice',
params: {
item: data
}
}
break
case 'img': opt = {
name: 'noteShow',
params: {
item: data
}
}
break
default:
break
}
this.$router.push(opt)
},
getFileList (type, isTrash, val) {
axios.get(`/api/getfilelist`, {
params: {
id: this.userId,
type,
isTrash,
val: val || ''
}})
.then((res) => {
if (res.data.status) {
res.data.list.map(el => {
el.file_content = el.file_type === 'img' ? URL.createObjectURL(this.base64ToBlob(el.file_content)) : el.file_content
})
this.list = fileInfoTran(res.data.list)
console.log(this.list)
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
}
}
}



